@font-face {
  font-family: "Roboto-lite";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto-Thin";
  src: url("../fonts/Roboto-Thin.woff2") format("woff2"), url("../fonts/Roboto-Thin.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  scroll-behavior: smooth; }

.content-box-component {
  box-sizing: content-box; }

body {
  position: relative;
  height: 100%;
  margin: 0 auto;
  color: #000000;
  font-family: "Roboto-lite";
  font-size: 16px;
  line-height: 18px; }

a {
  text-decoration: none;
  color: inherit; }

ul,
ol,
li {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: top; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden; }

.container {
  min-width: 320px;
  max-width: 800px;
  padding: 0 20px;
  margin: 0 auto; }

.btn {
  display: flex;
  min-height: 40px;
  border-radius: 10px;
  padding: 5px 20px;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: black;
  background-color: #b9cdf1; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.footer {
  padding: 10px 0 10px 0;
  background: linear-gradient(90deg, white 0%, #dbe4f5 100%, #dbe4f5 100%);
  text-align: center; }

.header {
  position: fixed;
  z-index: 100;
  width: 100%;
  border-bottom: 1px solid #dbe4f5;
  background: linear-gradient(90deg, white 0%, #dbe4f5 100%, #dbe4f5 100%);
  transition: 0.5s; }
  .header--shadow {
    transition: 0.5s;
    box-shadow: 0px 8px 15px -5px #000000; }
  @media (max-width: 500px) {
    .header {
      min-height: 64px;
      margin-bottom: 0; } }

.navigation {
  padding: 5px 0; }

.navigation__link {
  text-decoration: none;
  color: #435993;
  font-size: 14px;
  line-height: 170%;
  border-bottom: 1px solid transparent;
  transition: 0.5s; }
  .navigation__link--current {
    color: #1c1c1c;
    border-bottom: 1px solid #1c1c1c;
    transition: 0.5s; }
  @media (max-width: 500px) {
    .navigation__link {
      font-size: 24px; } }

.navigation__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  min-height: 40px;
  align-items: center;
  justify-content: space-between; }
  @media (max-width: 500px) {
    .navigation__list {
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between; }
      .navigation__list--closed {
        display: none; }
      .navigation__list--opened {
        display: block; } }

.navigation__item:not(:last-child) {
  margin-right: 10px; }

.navigation__menu {
  display: none; }
  @media (max-width: 500px) {
    .navigation__menu {
      display: block;
      position: absolute;
      top: 15px;
      right: 15px;
      height: 30px;
      width: 30px;
      border-radius: 50%;
      cursor: pointer; }
      .navigation__menu--on {
        background-image: url("../img/svg/menu-on.svg");
        background-position: center center;
        background-repeat: no-repeat; }
      .navigation__menu--off {
        background-image: url("../img/svg/menu-off.svg");
        background-position: center center;
        background-repeat: no-repeat; } }

.main {
  flex: 1 1 auto;
  text-align: center;
  padding-top: 100px; }
  @media (max-width: 500px) {
    .main {
      padding-top: 60px; } }

.main__photo {
  border-radius: 50%; }
  @media (max-width: 500px) {
    .main__photo {
      border-radius: 0;
      width: 500px; } }

.main__title {
  font-family: "Roboto-Thin";
  font-size: 70px;
  line-height: 170%;
  margin: 0; }

.data {
  display: flex;
  flex-direction: column;
  align-items: center; }

.data__caption {
  text-align: center;
  font-family: "Roboto-Lite", sans-serif;
  font-size: 30px;
  line-height: 150%;
  font-weight: normal;
  border-bottom: 1px solid #b4c4e2; }

.data__year {
  text-transform: uppercase;
  font-weight: 900; }

.data__institution {
  font-weight: bold;
  font-style: italic; }

.data__list {
  width: 100%;
  list-style: none; }
  .data__list--info {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-between; }
  .data__list--skill {
    list-style: circle;
    display: flex;
    flex-wrap: wrap;
    max-height: 200px;
    padding-left: 30px;
    align-items: flex-start;
    flex-direction: column;
    font-size: 18px; }
    @media (max-width: 560px) {
      .data__list--skill {
        list-style: none;
        max-height: none;
        padding-left: 0;
        flex-direction: row;
        align-items: center; } }
    @media (max-width: 414px) {
      .data__list--skill {
        justify-content: space-around; } }
  @media (max-width: 560px) {
    .data__list--ed {
      list-style: circle;
      text-align: left;
      padding-left: 20px; } }
  .data__list--portfolio {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 350px));
    justify-content: center;
    grid-column-gap: 20px;
    grid-row-gap: 20px; }

.data__item--info {
  width: 49%;
  background: linear-gradient(90deg, white 0%, #dbe4f5 100%, #dbe4f5 100%);
  border: 1px solid #c0bcbc;
  border-radius: 5px;
  padding: 10px 5px 10px 5px;
  margin-bottom: 10px; }
  .data__item--info:first-child {
    width: 100%; }
  @media (max-width: 500px) {
    .data__item--info {
      width: 100%; } }

.data__item--skill {
  width: none;
  text-align: start;
  border-bottom: 1px solid #cad0dd; }
  @media (max-width: 560px) {
    .data__item--skill {
      width: 48%;
      margin-right: 0px;
      background: linear-gradient(90deg, white 0%, #dbe4f5 100%, #dbe4f5 100%);
      border: 1px solid #c0bcbc;
      border-radius: 5px;
      padding: 10px 15px 10px 15px;
      margin-bottom: 10px;
      text-align: center; }
      .data__item--skill:nth-child(odd) {
        margin-right: 15px; } }
  @media (max-width: 414px) {
    .data__item--skill {
      width: 47%; } }

.data__item--skill:not(:last-child) {
  margin-bottom: 10px; }

.data__item--portfolio {
  transition: 0.5s; }
  .data__item--portfolio:hover {
    transform: scale(1.1);
    transition: 0.5s; }

.data__textarea {
  width: 100%;
  height: 150px;
  padding: 20px;
  resize: none;
  margin-bottom: 30px;
  font-size: 18px;
  line-height: 150%;
  background: linear-gradient(90deg, white 0%, #dbe4f5 100%, #dbe4f5 100%); }
  @media (max-width: 560px) {
    .data__textarea {
      font-size: 16px;
      padding: 10px; } }

.data__wrapper {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center; }

.img-bg {
  position: relative; }

.img-bg img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover; }
